<template>
    <div class="merge-sql">
        <h4>请输入SQL:</h4>
        <el-input type="textarea" :rows="4" placeholder="请输入SQL" v-model="textarea1"> </el-input>
        <h4>请输入参数:</h4>
        <el-input type="textarea" :rows="4" placeholder="请输入参数" v-model="textarea2"> </el-input>
        <div>
            <el-button-group>
                <el-button type="primary" icon="el-icon-refresh" @click="merge">合并</el-button>
                <el-button type="primary" icon="el-icon-copy-document" @click="copy">复制</el-button>
                <el-button type="danger" icon="el-icon-delete" @click="clear">清空</el-button>
            </el-button-group>
        </div>
        <h4>合并结果:</h4>
        <el-input type="textarea" :rows="4" placeholder="显示合成结果" v-model="textarea3"> </el-input>
    </div>
</template>

<script>
/* eslint-disable camelcase */
const str_sql = '没有添加SQL语句,可不行啊!';
const str_param = '没有输入参数,要输入参数吆!';
const Fcopy_strone = '内容为空，不能复制!';

import { clipboard } from '@/utils/util.js';

export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
            textarea3: '',
        };
    },
    methods: {
        merge() {
            let sql = this.textarea1;
            let param = this.textarea2;

            if (!sql) {
                this.$message.error(str_sql);
                return;
            }
            if (!param) {
                this.$message.error(str_param);
                return;
            }

            sql = sql.trim();
            param = param.trim();

            if (param.indexOf('[') === 0) {
                param = param.substring(1);
            }

            if (param.indexOf(']') === param.length - 1) {
                param = param.substring(0, param.length - 1);
            }

            let params = param.split(',');
            let sqls = sql.split('?');
            let result = '';
            for (let i = 0; i < params.length; i++) {
                let currentParam = params[i].trim();
                if (currentParam.includes('(Integer)')) {
                    result += sqls[i] + currentParam;
                } else {
                    result += sqls[i] + "'" + currentParam + "'";
                }
            }
            if (sqls.length > params.length) {
                result += sqls[sqls.length - 1];
            }

            result = result.replaceAll('(String)', '');
            result = result.replaceAll('(Integer)', '');

            this.textarea3 = result;
        },
        clear() {
            this.textarea1 = '';
            this.textarea2 = '';
            this.textarea3 = '';
        },
        copy() {
            const value = this.textarea3;
            if (!value) {
                this.$message.error(Fcopy_strone);
                return;
            }
            clipboard(value);
            this.$message.success('复制成功');
        },
    },
};
</script>

<style lang="scss" scoped>
.merge-sql {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
</style>
